import React, { CSSProperties } from 'react';
import ITextState from './../_states/text.state';

export default class Paragraph extends React.Component<ITextState, ITextState> {
    state: ITextState;

    constructor(props: ITextState) {
        super(props);
        this.state = { ...props };
    }

    render(): JSX.Element {
        return <h1 style={this.ParagraphStyle}>{this.state.text}</h1>;
    }

    get ParagraphStyle() {
        return {
            fontSize: '22px',
            fontWeight: 300,
            marginBottom: '6px',
            marginLeft: '8px'
        } as CSSProperties;
    }
}



