import React from 'react';
import './App.css';
import { LandingPage } from './_pages/LandingPage';
import { Link, Element, scrollSpy, animateScroll as scroll } from 'react-scroll';
import Welcome from './_sections/Welcome.section';
import LookingBack from './_sections/LookingBack.section';
import ListenSpeak from './_sections/ListenSpeak.section';
import MovingForward from './_sections/MovingForward.section';
const SMALL_LOGO = require('./_images/ERVS_240_REG.png');

export default class App extends React.Component {
    public render() {
        return (
            <div className="wrapper_app">
                <div className="wrapper_nav">
                    <nav>
                            <img onClick={scroll.scrollToTop} className="nav_small-logo" src={SMALL_LOGO} />
                        <div className="wrapper_nav-links">
                            <Link
                                activeClass="active"
                                to="Welcome"
                                spy={true}
                                smooth={true}
                                offset={-250}
                                duration={500}
                            >
                                About
                            </Link>
                            <Link
                                className="btn_get-started"
                                activeClass="active"
                                to="LookingBack"
                                spy={true}
                                smooth={true}
                                offset={-250}
                                duration={500}
                            >
                                Get started
                            </Link>
                            <Link
                                activeClass="active"
                                to="ListenSpeak"
                                spy={true}
                                smooth={true}
                                offset={-250}
                                duration={500}
                            >
                                Connect
                            </Link>
                            <Link
                                activeClass="active"
                                to="MovingForward"
                                spy={true}
                                smooth={true}
                                offset={-250}
                                duration={500}
                            >
                                Resources
                            </Link>
                        </div>
                    </nav>
                </div>
                <div className="wrapper_content">
                    <LandingPage />
                    <div className="wrapper_sections">
                        <Element name="Welcome">
                            <Welcome />
                        </Element>
                        <Element name="LookingBack">
                            <LookingBack />
                        </Element>
                        <Element name="ListenSpeak">
                            <ListenSpeak />
                        </Element>
                        <Element name="MovingForward">
                            <MovingForward />
                        </Element>
                    </div>
                </div>
            </div>
        );
    }

    public componentDidMount() {
        scrollSpy.update();
    }
}

export const getRandomNumber = function(): number {
    return Math.floor(Math.random() * 100);
};


