import React from 'react';
import Heading from './../_components/Heading';
import Paragraph from '../_components/Paragraph';
import { getRandomNumber } from '../App';

// tslint:disable:max-line-length
const text = [
    `
    The site asks users to enter into the headspace of a persons with radically different backgrounds, identities, and beliefs, and to sacrifice their vote as a means of expressing a solidarity which transcends the arbitrary categories dictated by our social conventions. This notion of “solidarity through sacrifice” ties into my experience at CWA. It has not been easy to make time to volunteer at that facility, this being the most stressful semester of my academic career, but the possibility of impacting a young life for the better has generated a hopefulness which is its own reward.     `,
    `
    Initially, I was hesitant to place myself in the position of “mentor,” “caregiver,” and “educator” in relation to the CWA kids. Perhaps the greatest challenge was owning up to the responsibilities my privileged status in our society places on me. I often thought of the innocence of those young children, and the degree to which my interaction with them would prove beneficial  or detrimental. But I’d like to think that my academic training in the liberal arts has boosted my self-confidence and shown me that my own perspective is inherently valuable to others. 
    `
];

export default class ListenSpeak extends React.Component {
    public render(): JSX.Element {
        const ps = text.map((p: string) => {
            return <Paragraph key={getRandomNumber()} text={p} />;
        });
        return (
            <div>
                <Heading text="Solidarity and sacrifice" />
                {ps}
            </div>
        );
    }
}



