import React from 'react';
import FontAwesome from 'react-fontawesome';
import { Link } from 'react-scroll';
import './_pages.css';
const images = [
    require('./../_images/prisoner_one.png'),
    require('./../_images/prisoner_two.png'),
    require('./../_images/prison_cell.png'),
    require('./../_images/tv.png')
];

interface ILandingPageProps {}

export class LandingPage extends React.Component {
    private carouselElement: HTMLDivElement;
    private imageIndex = 0;

    constructor(public props: ILandingPageProps) {
        super(props);
    }
    componentDidMount(): void {
        try {
            if (this.initCarousel() !== undefined) {
                window.setInterval(this.nextImage.bind(this), 10000);
            }
        } catch (exception) {
            console.error(
                'Problem initializing carousel. Element query returned zero results.'
            );
        }
    }
    render(): JSX.Element {
        return (
            <div id="carousel" className="wrapper_carousel">
                <h2>
                    'We can continue along this road--it is a road well
                    traveled--but we must admit the strategy has not made much
                    of a difference.' -- Michelle Alexander,{' '}
                    <em>The New Jim Crow</em>
                </h2>
                <h1>
                    Vote on behalf of a prisoner in<br />the '18 midterm
                    elections
                </h1>
                <Link
                    className="btn_sign-up"
                    activeClass="active"
                    to="Welcome"
                    spy={true}
                    smooth={true}
                    offset={50}
                    duration={500}
                >
                    <FontAwesome
                        style={{ marginRight: '8px' }}
                        name="list-alt"
                    />Become a voter proxy
                </Link>
            </div>
        );
    }

    private initCarousel(): HTMLDivElement | undefined {
        this.carouselElement =
            this.carouselElement !== undefined
                ? this.carouselElement
                : (document.getElementById('carousel') as HTMLDivElement);
        if (this.carouselElement !== undefined) {
            this.carouselElement.style.transition = 'all 5s linear';
        }
        return this.carouselElement;
    }

    private nextImage(): void {
        this.imageIndex = this.imageIndex === 0 ? 1 : this.imageIndex + 1;
        if (this.imageIndex > images.length - 1) {
            this.imageIndex = 0;
        }
        const newImageUrl = images[this.imageIndex];
        this.carouselElement.style.backgroundImage = `url('${newImageUrl}')`;
    }
}



