import React, { CSSProperties } from 'react';
import ITextState from './../_states/text.state';
export default class Heading extends React.Component<ITextState, ITextState> {
    public state: ITextState;

    constructor(props: ITextState) {
        super(props);
        this.state = { ...props };
    }

    render(): JSX.Element {
        return <h1 style={this.headingStyle}>{this.state.text}</h1>;
    }

    get headingStyle() {
        return {
            fontSize: '36px',
            fontWeight: 300,
            margin: '48px 0 24px',
            textTransform: 'uppercase'
        } as CSSProperties;
    }
}



