import React from 'react';
import Heading from './../_components/Heading';
import Paragraph from '../_components/Paragraph';
import { getRandomNumber } from '../App';

const text = [
    `
    I call this idea "Pardon Democracy"! It's meant to be a platform
    for proactive citizens who want to disrupt the established systems of
    criminal justice and reform in America. It may not seem like much, but in
    the world of web development simple prototypes like this are how big ideas
    get started.
    `,
    `
    In my imagination, eligible voters would use the site to engage with
    current and released prisoners who are, by nature of their convictions,
    disallowed from voting in our elections. Using a kind of correspondence or
    'chat' feature, voters would enter into a verbal contract with prisoners,
    agreeing to vote for the candidates of the prisoner's choosing.
    `,
    `
    This idea feels interesting because it poses questions not only about
    America's system of mass incarceration, but the value individuals place
    on their right to vote. Additionally, the idea challenges the principle of voting
    for candidates out of self-interest. Finally, Pardon Democracy raises questions
    about who is “speaking” when a ballot is cast. 
    `
];

export default class Welcome extends React.Component {
    public render(): JSX.Element {
        const ps = text.map((p: string) => {
            return <Paragraph key={getRandomNumber()} text={p} />;
        });
        return (
            <div>
                <Heading text="Welcome professor" />
                {ps}
            </div>
        );
    }
}



