import React from 'react';
import Heading from './../_components/Heading';
import Paragraph from '../_components/Paragraph';
import { getRandomNumber } from '../App';

// tslint:disable:max-line-length
const text = [
    `
    I hope that on some level this project speaks to the effect a liberal arts education has had on my thinking. I can say with certainty that the liberal arts core curriculum at UNCA has engendered a habit of abstaining from judgement and inquiring into matters without the need for a clear “answer,” but for the sake of pursuing some ineffable “truth.” UNCA served me well by introducing me to a variety of disparate disciplines, many of which I might have disregarded in my youth, and Pardon Democracy synthesizes the humanitarian compassion UNCA’s curriculum has fostered within me with the calculated objectivism required to succeed in my professional life as a software engineer. 
    `,
    `
    Pardon Democracy also represents a decision to investigate foreign perspectives rather than presume one’s thoughts and feelings are the only factors to consider when making decisions. Our course materials and group discussions this semester have demonstrated that the world is filled with brilliant, passionate people, each with the capacity to share their “story” and affect change in the world. I’ve incorporated this lesson into the Pardon Democracy project, which would facilitate an open dialogue between voter proxies and the prisoners they speak for at the polls. 
    `
];

export default class LookingBack extends React.Component {
    public render(): JSX.Element {
        const ps = text.map((p: string) => {
            return <Paragraph key={getRandomNumber()} text={p} />;
        });
        return (
            <div>
                <Heading text="Exploring perspectives" />
                {ps}
            </div>
        );
    }
}



