import React from 'react';
import Heading from './../_components/Heading';
import Paragraph from '../_components/Paragraph';
import { getRandomNumber } from '../App';

// tslint:disable:max-line-length
const text = [
    `
    It’d be arrogant of me to proclaim that I’ve identified the role I should play in this world. Just when I think I have it pinned down, a new perspective comes along which forces me to reconsider my assumptions. I think I possess certain aptitudes which I could put to good use, specifically in the realm of technology. I feel I have the technological “know-how” to create platforms which foster productive dialogues between disparate perspectives. I’m interested in giving marginalized people a voice in our mainstream discourses, and technology holds great promise in this area.
    `,
    `
    I suppose I view my role as a facilitator, a coordinator of meaningful and productive dialogues between those in the center and those on the periphery. Our readings this semester have demonstrated that the role is a very important one, as so many peoples are left by the wayside as our civilization putters along.
    `,
    `
    Today, I hope to give a voice to our nation’s prisoners, but the space of the margins is constantly replenishing itself. My advocacy for the outsiders can never be over, and this course and my education at UNCA have prepared me to take on the never-ending challenge of giving a voice to the silenced.
    `,
    `Professor Porter, I'd like to thank you personally for the hard word you've dedicated to our class this semester, and I hope you realize how much your efforts mean to your students as they move forward in life. Have a great winter break!`
];

export default class MovingForward extends React.Component {
    public render(): JSX.Element {
        const ps = text.map((p: string) => {
            return <Paragraph key={getRandomNumber()} text={p} />;
        });
        return (
            <div>
                <Heading text="Moving forward" />
                {ps}
            </div>
        );
    }
}



